* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.article{
    clear: both;
    width: 50%;
    margin-left: 25%;
}

#root{
    background-color: #add8e630;
    color: #000000e0;
}

html {
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
}

body{
    background-repeat: no-repeat;
    font-size: 2rem;
    line-height: 1.5;
    min-height: 50rem;
    color: white;
    margin: 0;
}

.img-rounded{
    border-radius:50%;
    width: 12rem;
    border: white solid 0.125rem;
    display: inline-block;
    text-align: center;
}

.profile{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    float: left;
    margin: 1rem 8rem;
}
ul.navbar{
    float: right;
    margin-right: 10rem;
}

ul.navbar li{
    background: #add8e630;
    border-radius: 0.2rem;
    border: 0.1rem white solid;
    padding: 1rem;
    display: inline;
    margin: 1rem;
    cursor: pointer;
}

.details-pane{
    margin: 2rem;
    padding: 2rem;
}

.hire-me-btn{
    width: 50%;
    background: transparent;
    margin: 1rem auto;
    padding: 1rem;
    border: 0.125rem solid yellow;
    border-radius: 2rem;
    text-align: center;
    display: block;
}
.intro{
    font-size: 4rem;
    font-weight: bold;
}
.center-align{
    text-align: center;
}

.button{
    height: auto;
    line-height: normal;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    -webkit-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
    letter-spacing: 0;
}

.margin2{
    margin: 2rem auto;
}
.margin5{
    margin: 5rem auto;
}

.display-block{
    display: block;
}

.float-left{
    float: left;
}

.about-me{
    background: #bbf2f836;
    display: inline-block;
    padding: 5rem;
    margin: 0rem;
    height: 100vh;
}

.landing-page{
    background: url("images/bg-image.jpeg");
    background-size: cover;
    width: 100%;
    float: left;
    height: 100vh;
    color: white;
    font-size: 2rem;
}
.tech-card {
    text-align: center;
    background: #b5eef28f;
    padding: 2rem 0;
    border-radius: 5rem 0;
    margin: 5%;
}

.tech-cards ul{
    margin: 0 auto;
}

input:focus{
    outline: none;
}

.tech-cards ul li{
    float: left;
    text-align: center;
    padding: 0 1rem;
    color: rebeccapurple;
    background: #ff005285;
    height: 10rem;
    width: 20rem;
    margin: 1rem;
    padding: 3rem;
    border-radius: 0.5rem;
}

.color-black{
    color: black;
}

.nav-button{
    border: none;
    background: transparent;
    font-size: 5rem;
}

.disp-inln-bl{
    display: inline-block;
}

.work-experience{
    background-size: cover;
    background: #1ad2323b;
    width: 100%;
    height: 100vh;
    display: inline-block;
}

.hidden{
    display: none;
}

.company-logo{
    width: 10rem;
}

.company-tile{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.pie-chart{
    width: 20rem;
    height: 10rem;
}

.carousel{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.flex-grow-2{
    -ms-flex-positive: 2;
        flex-grow: 2;
    padding:1rem;
}

.flex-grow-1{
    -ms-flex-positive: 1;
        flex-grow: 1;
    padding:1rem;
}

.font-medium{
    font-size: medium;
}

.font-large{
    font-size: large;
}

.twitter-link{
    padding: 2rem;
}
.social{
    background: #f26a065e;
    height: 30vh;
}


